*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-color: #d41443;
  --sec-color: #ffa5ba;
  --bg-circle: #ffb8ca;
  --button-hover-bgc: #ecebeb;
  --button-red-bgc: #d41342;
  --white-text-color: #fff;
  --sec-text-color: #907e82;
  --black-text-color: #000;
  --first-bgc: #e5e5e5;
  --sec-bgc: #fff5f6;
  --customer-reviews-bgc: #e1e1e1;
  --first-additional-color: #f0d1a5;
  --second-additional-color: #ffb8ca;
  --third-additional-color: #c2e297;
  --modal-color: #e17992;
  --acent-color: #2196f3;
  --secondary-color: #188ce8;
  --modal-form: #21212133;
  --header-color: #212121;
  --anim-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--black-text-color);
  font-family: DM Sans, sans-serif;
  font-style: normal;
  font-weight: 500;
}

html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

h1, h2, h3, p, ul, a, span, button {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.menu-btn {
  width: 209px;
  height: 44px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 22px;
}

.menu-btn_text {
  color: #d41443;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.menu-btn_icon {
  fill: #d41443;
}

.button {
  justify-content: center;
  align-items: center;
  display: flex;
}

.button .modal-btn {
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: -8px 0 20px #00000040;
}

