  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button .button--white {
  color: var(--main-color);
  background-color: var(--white-text-color);
}

.button .button--white:hover, .button .button--white:focus {
  background-color: var(--button-hover-bgc);
}

.button .button--red {
  color: var(--white-text-color);
  background-color: var(--button-red-bgc);
}

.menu-btn_buy-now {
  width: 209px;
  height: 44px;
  cursor: pointer;
  color: var(--main-color);
  transition: background-color var(--anim-slow);
  background-color: #fff;
  border: none;
  border-radius: 22px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.menu-btn_buy-now:hover, .menu-btn_buy-now:focus {
